import React, {useState} from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import NavBreadcrumbs from '../components/NavBreadcrumbs';
import CategoryPill from '../components/CategoryPill';
import useIsMobile from '../hooks/isMobile';
import FavoritesIcon from '../components/svg/FavoritesIcon';
import {Link} from 'react-router-dom';

const Product = () => {
  const {mobile} = useIsMobile('991px');
  const [fav, setFav] = useState(false);

  return (
    <main>
      <Container>
        <NavBreadcrumbs/>

        {
          (!mobile) &&
          <section className='sec-8 mb-custom'>
            <ul>
              <li><CategoryPill img={"imgs/img2.jpg"} title={'  '}/></li>
              <li><CategoryPill img={"imgs/img3.jpg"} title={' '}/></li>
              <li><CategoryPill img={"imgs/img4.jpg"} title={''}/></li>
              <li><CategoryPill img={"imgs/img5.jpg"} title={'  '}/></li>
              <li><CategoryPill img={"imgs/img6.jpg"} title={'  '}/></li>
              <li><CategoryPill imgClassName={'bg'} img={"imgs/img7.png"} title={''}/></li>
            </ul>
          </section>
        }

        <section className='sec-12 mb-sm-4 mb-md-5'>
          <Row className='g-4 g-xxl-5'>
            <Col sm={4} lg={3}>
              <img src="imgs/img13.png" alt="HIGHTEC GTS SPEZIAL SAE 20W-50"/>
            </Col>
            <Col sm={8} lg={5} xxl={6}>
              <h1 className='inner'>HIGHTEC GTS SPEZIAL SAE 20W-50</h1>
              <small className='fw-5'>: 3630477</small>
              <ul className='specification list-unstyled'>
                <li><strong className='me-2 me-xl-3'> </strong> Hightec</li>
                <li><strong className='me-2 me-xl-3'></strong> API SF/CD,CCMC D2/G2,MIL-L-2104D,SAE 20W-50</li>
                <li><strong className='me-2 me-xl-3'>  SAE</strong> 20W-50</li>
                <li><strong className='me-2 me-xl-3'> </strong> </li>
                <li><strong className='me-2 me-xl-3'> </strong> , </li>
              </ul>
            </Col>
            <Col sm={{span:8, offset:4}} lg={{span:4, offset:0}} xxl={3}>
              <h4> </h4>
              <ul className='packaging-list'>
                <li>
                  <label>
                    <input type="radio" name="packaging" hidden/>
                    <div>1</div>
                  </label>
                </li>
                <li>
                  <label>
                    <input type="radio" name="packaging" hidden/>
                    <div>5</div>
                  </label>
                </li>
                <li>
                  <label>
                    <input type="radio" name="packaging" hidden/>
                    <div>20</div>
                  </label>
                </li>
              </ul>
              <div className="box mt-4">
                <div>
                  <h4>930 </h4>
                  <h6>1 140 </h6>
                </div>
                <button type='button' className='btn-2'> </button>
                <button type='button' className={(fav)?'btn-fav checked':'btn-fav'} onClick={()=>setFav(!fav)}>
                  <FavoritesIcon/>
                </button>
              </div>
              <small className='d-block fw-5'><Link to='/catalog' className='link'>     </Link></small>
            </Col>
            <Col sm={12} lg={9}>
              <div className="text">
                <p>  ,       SAE 20W-50.</p>
                <ul>
                  <li>   </li>
                  <li>   ,    , </li>
                  <li>     </li>
                  <li>    </li>
                  <li>      </li>
                  <li>    </li>
                  <li>           .     ,     .</li>
                </ul>
              </div>
            </Col>
          </Row>
        </section>
      </Container>
    </main>
  );
};

export default Product;