import React from 'react';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import CategoryCard from '../components/CategoryCard';
import Rowe from '../assets/imgs/rowe.svg';
import {Link} from 'react-router-dom';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import OfferCard from '../components/OfferCard';
import NewsPreview from '../components/NewsPreview';
import Cursor from '../components/svg/Cursor';

const Home = () => {
  return (
    <main>
      <Container>
        <section className='sec-1'>
          <h1>  </h1>
          <button type='button' className='btn-1 mx-auto'> </button>
        </section>
      </Container>

      <section className='sec-2'>
        <Container>
          <div className='left'>
            <h4 className='title-1'>
              <span> <br/> </span>
              {/* <img src="imgs/icons/circle.svg" alt="circle" /> */}
            </h4>
            <h4 className='title-2'>
              <span> </span>
              </h4>
          </div>
          <img src="imgs/img1.png" alt="engine" className='engine'/>
          <div className='right'>
            <h4 className='text-md-end title-3'><br/>  </h4>
            <h4 className='text-md-end title-4'> </h4>
          </div>
        </Container>
      </section>

      <Container>
        <section className='sec-3 mb-custom'>
          <h3> </h3>
          <Row className="gx-3 gy-3 gy-sm-4">
            <Col xs={12} lg={3}>
              <Row xs={1} md={2} lg={1} className="gx-3 gy-4">
                <Col><CategoryCard img={"imgs/img2.jpg"} title={'  '}/></Col>
                <Col><CategoryCard img={"imgs/img3.jpg"} title={' '}/></Col>
              </Row>
            </Col>
            <Col xs={12} lg={3}>
              <Row xs={2} lg={1} className="gx-3 gy-4">
                <Col><CategoryCard img={"imgs/img4.jpg"} title={''}/></Col>
                <Col><CategoryCard img={"imgs/img5.jpg"} title={'  '}/></Col>
              </Row>
            </Col>
            <Col xs={6} lg={3}><CategoryCard className={'bigger'} img={"imgs/img6.jpg"} title={'  '}/></Col>
            <Col xs={6} lg={3}>
              <CategoryCard className={'big'} img={"imgs/img7.png"} title={''}/>
              <button type='button' className='btn-2 w-100 d-none d-lg-block mt-lg-5'> </button>
            </Col>
          </Row>
        </section>

        <section className='sec-4 mb-custom'>
          <h3> </h3>
          <div className='position-relative'>
            <Cursor className="cursor-animated"/>
            <Swiper
              spaceBetween={20}
              slidesPerView={1}
              breakpoints={{
                576: {
                  slidesPerView: 2,
                  spaceBetween: 20,
                },
                768: {
                  slidesPerView: 3,
                  spaceBetween: 20,
                },
                992: {
                  slidesPerView: 4,
                  spaceBetween: 20,
                },
                1200: {
                  slidesPerView: 4,
                  spaceBetween: 30,
                },
                1400: {
                  slidesPerView: 4,
                  spaceBetween: 40,
                },
              }}
            >
              <SwiperSlide>
                <OfferCard title={'   HIGHTEC RACING MOTOR OIL SAE 10W-40'} img={"imgs/img8.png"}/>
              </SwiperSlide>
              <SwiperSlide>
                <OfferCard title={' HIGHTEC ANTIFREEZE AN G11'} img={"imgs/img10.png"}/>
              </SwiperSlide>
              <SwiperSlide>
                <OfferCard title={'   HIGHTEC LHM-PLUS'} img={"imgs/img11.png"}/>
              </SwiperSlide>
              <SwiperSlide>
                <OfferCard title={'  HIGHTEC SUNLUB FORM 46P'} img={"imgs/img12.png"}/>
              </SwiperSlide>
            </Swiper>
          </div>
        </section>

        <section className='sec-5 mb-custom'>
          <Row>
            <Col xs={12} md={7}>
              <h2 className='h1'>   HIGHTEC SYNT RSV SAE 0W-20</h2>
              <h5>    HIGHTEC RACING GREASEGUARDRLF2</h5>
            </Col>
            <Col xs={12} md={5} className="mt-4 mt-md-0">
              <img src="imgs/img9.png" alt="HIGHTEC SYNT RSV SAE 0W-20" />
            </Col>
          </Row>
        </section>

        <section className='sec-6 mb-custom'>
          <h2>   </h2>

          <ul className="news-list">
            <li>
              <NewsPreview img={"imgs/img7.jpg"} title={"7   "}/>
            </li>
            <li>
              <NewsPreview img={"imgs/img7.jpg"} title={"   ?"}/>
            </li>
            <li>
              <NewsPreview img={"imgs/img7.jpg"} title={" . ?"}/>
            </li>
            <li className='flex-1'>
              <Link className='link' to='/blog'> </Link>
            </li>
          </ul>
        </section>

        <section className='sec-7 mb-sm-3 mb-md-5'>
          <Row xs={1} lg={2} className='align-items-center'>
            <Col>
              <h2 className='d-flex align-items-baseline'>
                <span className='me-3'> </span> 
                <img src={Rowe} alt="Rowe"/>
              </h2>
              <h5>,   .         .  ,    .   ?</h5>
            </Col>
            <Col className="ps-lg-5 mt-4 mt-md-5 mt-lg-0">
              <div className="video-wrap">
                <iframe src="https://www.youtube.com/embed/7o4j42V21qs" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture; web-share" allowfullscreen></iframe>
              </div>
            </Col>
          </Row>
        </section>
      </Container>
    </main>
  );
};

export default Home;